import agent from './agent';
import jwtdecode from 'jwt-decode';

const promiseMiddleware = store => next => action => {
    if(isPromise(action.payload)){
        store.dispatch({type: 'ASYNC_START', subtype: action.type });
        action.payload.then(
            res => {
                action.payload = res;
                store.dispatch(action);
            },
            error => {
                action.error = true;
                action.payload = error.response.body;
                store.dispatch(action);
            }
        );

        return;
    }

    next(action);
};

function isPromise(v) {
    return v && typeof v.then === 'function';
}

const localStorageMiddleware = store => next => action => {
    if(action.type==='REGISTER' || action.type === 'LOGIN'){
        if(!action.error){
            console.log(action.payload);
            window.localStorage['jwt'] =  action.payload.token;
            agent.setToken(action.payload.token);
            const parsedToken = jwtdecode(action.payload.token);
            action.payload.user = parsedToken.username;
            action.payload.email = parsedToken.email;
        }
    } else if( action.type ==='LOGOUT'){
        window.localStorage.setItem('jwt', '');
        agent.setToken(null);
    }

    next(action);
}
export {
    localStorageMiddleware,
    promiseMiddleware
};


