import superagentPromise from 'superagent-promise';
import _superagent from 'superagent';
import { request } from 'http';
import jwtdecode from 'jwt-decode';


const superagent = superagentPromise(_superagent, global.Promise);

const API_ROOT = '/api';

const responseBody = res=> res.body;
let token = null;

const tokenPlugin = req => {
    if (token) {
      req.set('Authorization', `JWT ${token}`);
    }
  }

const requests = {
    get: url => 
        superagent.get(`${API_ROOT}${url}`).use(tokenPlugin).then(responseBody),
    post: (url, body) =>
        superagent.post(`${API_ROOT}${url}`, body).use(tokenPlugin).then(responseBody),
    put: (url, body) =>
        superagent.put(`${API_ROOT}${url}`, body).use(tokenPlugin).then(responseBody),
    del: url => 
        superagent.del(`${API_ROOT}${url}`).use(tokenPlugin).then(responseBody)
}

const limit = (count, p) => `limit=${count}&offset=${p ? p * count: 0}`;
const encode = encodeURIComponent;
const omitSlug = article => Object.assign({}, article, {slug: undefined});
const Course = {
    all: () =>
        requests.get(`/v2.4/courses/`),
    get: slug=>
       requests.get(`/v2.4/courses/${slug}`),
    getChapters: slug => 
        requests.get(`/v2.4/courses/${slug}/chapters`),
    getContents: slug => 
        requests.get(`/v2.4/courses/${slug}/contents/`)
    
      
};

const Auth = {
    current: () =>
        jwtdecode(localStorage.getItem('jwt')).email,
    login: (email, password) => { 
        console.log(email, password);
        return requests.post('/v2.2/auth-token/', { username: email, password })
},
    logout: () => 
        requests.post(`/v2.4/auth/logout/`)
};

export default {
    Course,
    Auth,
    setToken: _token => {
        token = _token;
    }
};


