import React from 'react';
import ArticlePreview from './ArticlePreview';
import ListPagination from './ListPagination';

const ArticleList = props => {
    console.log(props)
    if(!props.courses){
        return (
            <div className="article-preview">Loading...</div>
        );
    }

    if (props.selected && !props.courses.videos) {
        return (
            <div className="article-preview">Loading...</div>
        );
    }

    if(props.courses.length === 0){
        return (
            <div className="article-preview">No articles are here... yet.</div>
        );
    }

    return (
     props.tab === 'feed' ? 
     (<div>
         {
             props.courses.map((course, i)=>{
                 return (
                     <ArticlePreview id={course.id} course={course} key={course.id} />
                 );
             })
         }

         <ListPagination articlesCount={props.articlesCount} currentPage={props.currentPage} onSetPage={props.onSetPage} />
            </div>)
            : (
                <div>
         {
            props.courses.videos && props.courses.videos.map((course, i)=>{
                 return (
                     <ArticlePreview video={true} id={course.id} course={course} key={course.id} />
                 );
             })
         }

         <ListPagination articlesCount={props.articlesCount} currentPage={props.currentPage} onSetPage={props.onSetPage} />
            </div>
            )
 )
}

export default ArticleList;


