import React from 'react';
import {connect} from 'react-redux';
import ArticleList from './ArticleList';
import {Link} from 'react-router'
import agent from '../agent';

const FAVORITED_CLASS = 'btn btn-sm btn-primary';
const NOT_FAVORITED_CLASS = 'btn btn-sm btn-outline-primary';


const mapDispatchToProps = dispatch =>({
    favorite: slug => dispatch({
        type:'ARTICLE_FAVORITED',
        payload:agent.Articles.favorite(slug)
    }),
    unfavorite: slug => dispatch({
        type:'ARTICLE_UNFAVORITED',
        payload: agent.Articles.unfavorite(slug)
    }),
    setSelected: slug => dispatch({
        type: `SELECT_COURSE`,
        payload:{selected: slug}
    }),
    tabChange: (tab, payload) => {
        dispatch({ type: 'CHANGE_TAB', tab, payload })
    },

})
const ArticlePreview = props =>{
    const course = props.course;

    return (
        <div className="article-preview">
          <div className="article-meta">
           <Link to={``}>
            <img src={course.image} />
           </Link>

           <div className="info">
            <Link className="author" to={``}>
             {course.slug}
            </Link>
            <span className="date">
                { new Date(course.created).toDateString()}
            </span>
           </div>

            <div className="pull-xs-right">
             <button className="btn btn-sm btn-outline-primary">
              <i className="ion-heart"></i> { course[`videos_count`]}
             </button>
            </div>

          </div>
            { props.video!==true ? (
                <div onClick={() => {
                    console.log(props.course.id)
                    props.setSelected(props.course.id)
                    props.tabChange('course', agent.Course.getContents(props.course.id))
                }} className="preview-link">
                    <h1>{course.title}</h1>
                    <p>{course.description || `No Description Available`}</p>
                    <span>Read more...</span>
                </div>
            ) : (
            <Link to={`article/${btoa(JSON.stringify(course))}`} className="preview-link">
             <h1>{course.title}</h1>
             <p>{course.description}</p>
             <span>Read more...</span>
            </Link>
            )
            }

        </div>
    )
}

export default connect(()=>({}), mapDispatchToProps)(ArticlePreview);


