import ArticleList from '../ArticleList';
import React from 'react';
import { connect } from 'react-redux';
import agent from '../../agent';

const TagFilterTab = props => {
    if(!props.tag){
        return null;
    }
    return (
        <li className="nav-item">
         <a href="" className="nav-link active">
          <i className="ion-pound"></i> {props.tag}
         </a>
        </li>
    );
};
const YourFeedTab = props => {
    if(props.token){
        const clickHandler = ev => {
            ev.preventDefault();
            props.onTabClick('feed', agent.Course.all());
        }
    

    return (
        <li className="nav-item">
         <a href=""
          className={ props.tab === 'feed' ? 'nav-link active': 'nav-link'}
          onClick={clickHandler}>
          Your Courses
          </a>
        </li>
    );
    
 }
 return null;
}


const ChapterTab = props => {
    const clickHandler = ev =>{
        ev.preventDefault();
        props.onTabClick('all', agent.Course.getContents(props.selected));
    };

    return (
        <li className="nav-item">
         <a href="" className={ props.tab === 'course' ? 'nav-link active' : 'nav-link' }
         onClick={clickHandler}>
         Course
         </a>
        </li>
    )
}
const mapStateToProps = state => ({
   ...state.courseList,
   token : state.common.token
})

const mapDispatchToProps = dispatch => ({
    onTabClick: (tab, payload) => {
        dispatch({ type: 'CHANGE_TAB', tab, payload })
    },
    onSetPage: (tab, p) => dispatch({
        type: 'SET_PAGE',
        page: p,
        payload: tab==='feed' ? agent.Articles.feed(p) : agent.Articles.all(p)
    })
});

const MainView = props => {
    console.log(props)
    const onSetPage = page => props.onSetPage(props.tab, page);
    return (
        <div className="col-md-9">
         <div className="feed-toggle">
          <ul className="nav nav-pills outline-active">
           <YourFeedTab token={props.token} tab={props.tab} onTabClick={props.onTabClick} />
           <ChapterTab selected={props.selected} tab={props.tab} onTabClick={props.onTabClick} />
          </ul>
         </div>
         <ArticleList tab={props.tab} coursesCount={props.coursesCount}  currentPage={props.currentPage} onSetPage={onSetPage} courses={props.courses} />
        </div>
    )
}

export default connect(mapStateToProps, mapDispatchToProps)(MainView);


